

import ToastPlugin from 'vux/src/plugins/toast/index.js'

import Vue from 'vue'
Vue.use(ToastPlugin)
export default {
data(){
return{
keyword:'',// 搜索关键字
}
},
created(){
if(this.$route.query.inputWord){
this.keyword = this.$route.query.inputWord
}
},
methods:{
toSearch(){
if(this.$route.path.indexOf('search$') == -1){
console.log("跳转到搜索页面",this.$route.path.indexOf('search'))

this.$router.push({name:'search',query:{inputWord:this.keyword}})
}
},
search(){

try{

if(this.keyword.trim() != ""){
let historySearch = []
if(localStorage.getItem('historySearch')){
historySearch = JSON.parse(localStorage.getItem('historySearch'))
}

let isRepeat = false
historySearch.forEach((item)=>{
if(this.keyword == item){


isRepeat = true
return ;
}
})
if(!isRepeat){
historySearch.unshift(this.keyword)
console.log("historySearch",historySearch,historySearch.length)
if(historySearch.length>10){

historySearch = historySearch.slice(0,10)
}

localStorage.setItem('historySearch',JSON.stringify(historySearch))
}
this.$router.push({name:'searchResult',query:{inputWord:this.keyword}})
}else {
this.$vux.toast.show({
type:'text',
text: '请输入您要搜索的商品',
width:'15em'
})
this.keyword = ""
}

}catch (e) {
console.log("出现异常",e)

localStorage.removeItem('historySearch')
}

}

}
}
